body {
  margin: 0; }

.demo-cont {
  overflow: hidden;
  position: relative;
  height: 100vh;
  perspective: 1500px;
  background: #000; }
  .demo-cont__credits {
    box-sizing: border-box;
    overflow-y: auto;
    z-index: 1;
    position: absolute;
    right: 0;
    top: 0;
    width: 400px;
    height: 100%;
    padding: 20px 10px 30px;
    background: #303030;
    font-family: "Emberly-Black";
    color: #fff;
    text-align: center;
    transition: transform 0.7s;
    transform: translate3d(100%, 0, 0) rotateY(-45deg);
    will-change: transform; }
    .credits-active .demo-cont__credits {
      transition: transform 0.7s 0.23333s;
      transform: translate3d(0, 0, 0); }
    .demo-cont__credits *, .demo-cont__credits *:before, .demo-cont__credits *:after {
      box-sizing: border-box; }
    .demo-cont__credits-close {
      position: absolute;
      right: 20px;
      top: 20px;
      width: 28px;
      height: 28px;
      cursor: pointer; }
      .demo-cont__credits-close:before, .demo-cont__credits-close:after {
        content: "";
        position: absolute;
        left: 0;
        top: 50%;
        width: 100%;
        height: 2px;
        margin-top: -1px;
        background: #fff; }
      .demo-cont__credits-close:before {
        transform: rotate(45deg); }
      .demo-cont__credits-close:after {
        transform: rotate(-45deg); }
    .demo-cont__credits-heading {
      text-transform: uppercase;
      font-size: 40px;
      margin-bottom: 20px; }
    .demo-cont__credits-img {
      display: block;
      width: 60%;
      margin: 0 auto 30px;
      border-radius: 10px; }
    .demo-cont__credits-name {
      margin-bottom: 20px;
      font-size: 30px; }
    .demo-cont__credits-link {
      display: block;
      margin-bottom: 10px;
      font-size: 24px;
      color: #fff; }
    .demo-cont__credits-blend {
      font-size: 30px;
      margin-bottom: 10px; }

.example-slider {
